.todo-item {
 font-size: 24px;
 border-bottom: 1px solid #ededed;
 min-height: 60px;
 display: flex;
 align-items: center;
 list-style: none;
 padding: 0 16px 0 60px;
 cursor: pointer;
 display: flex;
 justify-content: space-between;
 align-items: center;
 position: relative;
}

.completed {
 text-decoration: line-through;
}

.completed,
.fa-check-circle {
 color: #ccc;
}

.fa-times {
 transition: all 0.3s ease;
 color: rgba(0, 0, 0, 0.15);
}

.mark {
 font-size: 20px;
 position: absolute;
 left: 20px;
}

.todo-item:hover .fa-times {
 color: rgba(0, 0, 0, 0.9);
}
